html,
body,
#root {
  height: 100%;
  margin: 0;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
}

.cakeLoop {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 12px;
  user-select: none;
}

.cakeLoop img,
.cakeLoop canvas {
  image-rendering: pixelated;
  image-rendering: crisp-edges;
  image-rendering: -moz-crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;

}

.birthdayText {
  width: auto;
  height: auto;
  max-width: 100vw;
  max-height: 30vh;
  image-rendering: pixelated;
}

:root {
  --cake-overlap: 24px;
  --cake-offset-x: 0px;
  /* horizontal offset for the cake (positive moves right) */
  --birthday-max-width: 10vw;
  /* default max width for birthday text */
}

.cake {
  position: relative;
  transform: translateY(calc(-1 * var(--cake-overlap))) translateX(var(--cake-offset-x));
  z-index: 2;
  padding-right: 200px;
}
.cake,
.cake img,
.cake canvas {
  cursor: crosshair;
}

.birthdayText {
  z-index: 1;
  max-width: 900px;
  max-height: 900px;
  position: relative;
}

.confetti-root {
  pointer-events: none;
  position: fixed;
  inset: 0 0 0 0;
  overflow: hidden;
  z-index: 9999;
}

.confetti-piece {
  position: absolute;
  top: -10vh;
  border-radius: 2px;
  opacity: 0.95;
  will-change: transform, top;
  animation-name: confetti-fall;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@keyframes confetti-fall {
  0% {
    transform: translateY(-10vh) rotate(0deg);
    opacity: 1;
  }

  70% {
    opacity: 1;
  }

  100% {
    transform: translateY(110vh) rotate(360deg);
    opacity: 0.85;
  }
}

.cake-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
  100% { transform: translateY(0px); }
}

.floating-cake {
  width: 400px; 
  cursor: pointer;
  image-rendering: pixelated; 
  animation: float 3s ease-in-out infinite;
  transition: transform 0.2s ease;
}

.floating-cake:active {
  transform: scale(0.9); 
}

.hint-text {
  color: #ff69b4;
  font-family: 'Courier New', monospace;
  margin-top: 40px;
  font-weight: bold;
}

.card-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500;700&display=swap');

.birthday-card {
  background: white;
  color: #333;
  padding: 40px;
  border-radius: 15px;
  height: 500px;
  max-width: 900px;
  text-align: center;
  border: 6px solid #ff8c69;
  box-shadow: 0 0 20px rgba(255, 105, 180, 0.5);
  font-family: 'Quicksand', sans-serif;
  font-size: 1.15rem;
}

.birthday-card h2 {
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
  color: #ff69b4; 
  text-shadow: 
    0 0 5px #fff,    
    0 0 10px #ff69b4, 
    0 0 20px #ff69b4; 
  margin-bottom: 10px;
}

.birthday-card p {
  font-family: 'Quicksand', sans-serif;
  line-height: 1.5;
}

.signature {
  font-family: 'Dancing Script', cursive;
  font-size: 1.4rem;
  color: #ffa569;
  margin-top: 30px;
  font-weight: bold;
}